import React from 'react'
import {Component} from 'react'
import './css/Header.css'
import Butterfly from '../images/butterfly.jpg'

class Intro extends Component {

	render() {
		let Data = this.props.Data;
		return (
			<div>
				<div className='row'>
					<div className='col-md-6'> 		
						<h3 className='intro'>About Me</h3>
						<p className="intro-des">I'm Mobarok Hossain Rubel. Full Stack developer, i like to code, and this is my porftfolio site</p>
					 </div>
					<div className='col-md-6'>
						<div className="text-center"> 
						<img src={Butterfly} alt="Developer picture" class="rounded introPic"></img>
						</div>
				
						 </div>
				</div>

			</div>
			)
		

	}
}

export default Intro;