import React, {Component} from 'react'
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import './css/Header.css'
import Mobarok from '../images/mobarok.jpg'

class Header extends Component {
    render(){
        return(
            <div>
                <div class="jumbotron">
                    <div className="row">
                        <div className="col-md-3">
                            <img src={Mobarok} alt="Developer picture" class="rounded-circle ppControl"></img>
                        </div> {/* col-md-8 */}
                        <div className="col-md-9"> 
                            <h1 className="display-6">
                                <span data-toggle="tooltip" data-placement="bottom" title="Hello">Hello</span>/
                                <span data-toggle="tooltip" data-placement="bottom" title="Ohe">ওহে</span>/
                                <span data-toggle="tooltip" data-placement="bottom" title="Marhabaan">مرحبا</span>/
                                <span data-toggle="tooltip" data-placement="bottom" title="Privet">Привет</span>/
                                <span data-toggle="tooltip" data-placement="bottom" title="Namaste">नमस्ते</span>/
                                <span data-toggle="tooltip" data-placement="bottom" title="Bonjour">Bonjour</span>!
                            </h1>
                            <hr className="my-4"></hr>
                            <h3 className="display-4">Mobarok Hossain Rubel</h3>
                            <span className="lead">- Developer / Engineer </span>
                        </div> {/* col-md-4 */}
                    </div> {/* row */}
                </div>{/* jumbotron */}
            </div>
        )
    }
}
export default Header